*{
  padding:0;
  margin: 0;
  box-sizing: border-box;
}
.container{
  margin: 0;
  padding: 0;
  position: absolute;
  background: url('./img/bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}
.container>.header{
  display: flex;
  margin: 15px 40px;
  justify-content: space-between;
  color: white;
}
.container>.header>input{
  width: 16rem;
  height: 2rem;
  background: rgb(41, 50, 50);
  border: none;
  color: white;
  padding: 5px;
  border-radius: 8px;
}
.container>.header>h2{
  font-style: cursive;
  color: white;
  font-weight: 800;
}

.item-list{
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  margin: 60px;
}
.item-list>.item{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  cursor: pointer;
}
.item-list>.item>img{
  width: 14rem;
  border-radius: 25px;
}
.item-list>.item>h3{
 color: #fff;
 font-size: 1.5rem;
 font-weight: 700;
}
@media screen and (max-width:460px) {
  .item-list{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin: 20px;
  }
  .item-list>.item>img{
    width: 10rem;
    border-radius: 25px;
  }
  .item-list>.item>h3{
    color: #fff;
    font-size: 1rem;
    font-weight: 700;
   }
}